
.mine_list_ul{
	text-align: left;
}
li{
	list-style: none;
	padding: 10px 20px;
	border-bottom: 1px solid #eee;
	position: relative;
	font-size: 14px;
}
li img{
	width: 20px;
	margin-right:20px;
}
.mine_list_ul span{
	float: right;
	font-size: 12px;
	color: #999;
}
.mine_mid_border{
	border-bottom: 10px solid #eee;
}
.mine_list_ul .unfinish{
	padding: 3px 10px;
	background-color: #00b38a;
	color: #fff;
	border-radius: 20px;
}
.mine_list_ul i{
	display: block;
	height: 8px;
	width: 8px;
	background-color: red;
	position: absolute;
	left: 100px;
	top: 12px;
	border-radius: 50%;
}



