
.home_title{
	font-size: 0.28rem;
}
.home_det_top{
	font-size: .24rem;
}
.detailAmi-enter{
	opacity: 0;
	transition: all .5s;
	-webkit-transform: translateX(300px);
	        transform: translateX(300px);
}
.detailAmi-enter-active{
	transition: all .5s;
}
.detailAmi-leave{
	opacity: 1;
	transition: all .5s;
}
.detailAmi-leave-active{
	opacity: 0;
	transition: all .5s;
	-webkit-transform: translateX(-300px);
	        transform: translateX(-300px);
}
dt{
	padding: 0.17rem 0;
	font-size: 0.28rem;
}
dd{
	line-height: 0.43rem;
	font-size: 0.24rem;
	color: #666;
}
.office .office_icon{
	float: right;
	font-size: 0.24rem;
}
.office_txt{
	font-size: 0.21rem;
	color: gray;
	padding-top: 5px;
	float: left;
}
.work_des{
	width: 100%;
	border-top: 10px solid #eee;
	clear: both;
	padding-top: 10px;
}
.work_des h3{
	font-size: 0.28rem;
	line-height: 0.6rem;
}
.detailbox_main_name_text{
	font-size: 0.2rem;
	color: gray;
	clear: both;
	padding: 15px 0;
	border-bottom: 1px solid lightgray;
}
.detailbox_main{
	margin:10px 20px;
}
.detailbox_main_name .main_name_span{
	float: none;
	display: inline;
	color: red;
}
.detailbox_main_name{
	margin-top: 10px;
	padding-bottom: 60px;
}
.detailbox_main_name span{
	display: block;
	float: left;
}
.detailbox_main_name i{
	float: left;
	width: 0.22rem;
	height: 0.28rem;
	margin-right: 5px;
}
.name_list{
	margin-top: 10px;
	font-size: 0.24rem;
}
.officename{
	font-size: 0.24rem;
}
.address_icon{
	
	background-image: url("../assets/images/detail_icon_05.jpg");
	background-repeat: no-repeat;
	background-position: 0 3px;
}
.working_icon{
	background-image: url("../assets/images/detail_icon_12.jpg");
	background-repeat: no-repeat;
	background-position: 0 3px;
}
.school_icon{
	background-image: url("../assets/images/detail_icon_14.jpg");
	background-repeat: no-repeat;
	background-position: 0 4px;
}
.job_icon{
	background-image: url("../assets/images/detail_icon_07.jpg");
	background-repeat: no-repeat;
	background-position: 0 3px;
}
.detail{
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	z-index: 100;
	overflow: auto;
	background-color: #fff;
}
.detailbox_top{
	width: 100%;
	height: 70px;
	background-color: #f7f7f7;
	line-height: 1.2rem;
	text-align: center;
}
.colseIcon{
	position: absolute;
	left: 20px;
	top: 4px;
	font-size: 0.34rem;
}
.home_main_list{
	
	text-align: left;
}
.home_main_list_top{
	position: relative;
	padding:15px;
	border-top: 10px solid #eee;
	border-bottom: 5px solid #eee;
}
.study_icon{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	float: left;
	padding:5px 10px;
	background-color: #eee;
	margin:10px 5px;
	font-size: 0.24rem;
}
.money_p{
	position: absolute;
	right: 0.86rem;
	top:0.34rem;
	font-size:0.28rem;
}
.working_text{
	font-size: 0.2rem;
}
.working_text span{
	color: #666;
}
.job_text{
	font-size: 0.24rem;
	padding-bottom: 5px;
}
.study_icon_box{
	height: 0.86rem;
}
.office_img{
	width: 0.6rem;
	float: left;
	padding-right: 10px;
}
.office{
	margin-top: 20px;
}
.office h5{
	font-size: 0.24rem;
	font-weight: normal;
}
.office p{
	font-size: 0.2rem;
	color: #999
}



