
.sub_spa_box{
	float: left;
	margin-right: 15px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding-top: 15px;
}
.subtxt{
	font-size: 0.2rem;
}
.speaktxt{
	font-size: 0.2rem;
}
.workdetail{
	font-size: 0.24rem;
	color: #666
}
.posi{
	width: 100%;
	height: 1.2rem;
}
.subicon{
	width: 0.4rem;
	height: 0.4rem;
}
.speakicon{
	width: 0.26rem;
	height: 0.26rem;
	padding-top: 5px;
	padding-right: 5px;
}
.worklist{
	text-align: left;
	padding:0 20px;
	margin-bottom: 1.2rem;
	overflow: hidden;
}
.work_list_item{
	width: 100%;
	border-bottom: 10px solid #eee;
	padding: 10px 0;
}
.work_list_item::after{
	content: "";
	display: block;
	clear: both;
}
.from_sty{
	font-size: 0.2rem;
	color: gray;
}
.workfrom{
	font-size: 0.24rem;
	color: #000;
}
.workdes{
	font-size: 0.24rem;
	padding-top: 5px;
}
.work_list_item_des{
	padding-top: 20px;
}
.worksrc{
	width: 0.52rem;
	float: left;
	padding-right: 8px;
}
.worker{
	padding:8px 0;
	font-size: 0.24rem;
	display: block;
}
.workname{
	font-style: normal;
	font-size: 0.2rem;
	padding-left: 20px;
	color: gray;
}
.workdate{
	float: right;
	color: lightgray;
}



