
.massagemain{
	padding-top: 70px;
	height: 130px;
	border-bottom: 6px solid #eee;
}
.msg_main_top_list{
	width: 33.33%;
	float: left;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	line-height: 60px;
}
.msg_main_top_list img{
	width: 50px;
	margin-top: 20px;
}
.msg_main_top_list span{
	font-size: 0.24rem;
}



